import React from "react";
import Navigation from "../../components/Navigation";
import Footer from "../../components/Footer";
import ItemInfo from "../../components/ItemInfo";
import LightBox from "../../components/Lightbox";
import asset1 from "../../images/14opryskiwacze-zawieszane/Klara 800/Klara 815 front.png";
import asset2 from "../../images/14opryskiwacze-zawieszane/Klara 800/Klara 815 back.PNG";
import PrizeBtn from "../../components/PrizeBtn";
import data from "../../data";

const info = data.filter((item) => {
  if (item.route === "/opryskiwacze-zawieszane/klara-800-tolmet") {
    return item;
  }
});
const Klara800 = () => (
  <div>
    <Navigation />
    <div className="wrapper">
      <h1 className="product-title">{info[0].name}</h1>
      <div className="description">
        <ul>
          <h3>Wyposaenie standardowe</h3>
          <li>
            <span>pojedyncze rozpylacze typu "V"</span>
          </li>
          <li>
            <span>zbiornik wykonany z OMEGA 800</span>
          </li>
          <li>
            <span>
              ocynkowana belka polowa o szerokoci 12m lub 14m lub 15m lub 16m
              lub 18m
            </span>
          </li>
          <li>
            <span>zbiornik do mycia rk o pojemnoci 15 litrw</span>
          </li>
          <li>
            <span>manometr</span>
          </li>
          <li>
            <span>rozdzielacz DURO 3-sekcyjny i 5-sekcyjny</span>
          </li>
          <li>
            <span>rozwadniacz rodkw chemicznych w sicie</span>
          </li>
          <li>
            <span>pompa o wydajnoci 140l/min</span>
          </li>
          <li>
            <span>mechaniczna regulacja wysokoci belki polowej</span>
          </li>
          <li>
            <span>atest do piciu lat</span>
          </li>
          <li>
            <span>rcznie rozkladana belka</span>
          </li>
        </ul>
        <ul>
          <h3>Wyposaenie dodatkowe</h3>
          <li>
            <span>potrjna oprawa rozpylacza</span>
          </li>
          <li>
            <span>rozdzielacz manualny Duro 5-sekcyjny</span>
          </li>
          <li>
            <span>rodzielacz staocinieniowy Fermo 5-sekcyjny</span>
          </li>
          <li>
            <span>rozpylacz eektorowy 0,2</span>
          </li>
          <li>
            <span>rozpylacz eektorowy 0,3</span>
          </li>
          <li>
            <span>owietlenie drogowe</span>
          </li>

          <li>
            <span>ukad napeniania zbiornika</span>
          </li>
          <li>
            <span>hydrauliczna stabilizacja belki polowej</span>
          </li>
          <li>
            <span>ukad zasysania cieczy do zbiornika</span>
          </li>
          <li>
            <span>hydrauliczna regulacja wysokoci belki polowej</span>
          </li>
          <li>
            <span>Control Panel 1(ON/OFF)</span>
          </li>
          <li>
            <span>Control Panel 2(ON/OFF, regulacja cinienia +/-)</span>
          </li>
          <li>
            <span>
              Control Panel 5(ON/OFF, regulacja cinienia +/-, sterowanie trzema
              sekcjami)
            </span>
          </li>
          <li>
            <span>
              Control Panel 7(ON/OFF, regulacja cinienia +/-, sterowanie
              picioma sekcjami)
            </span>
          </li>
          <li>
            <span>komputer Alfa 100(przy 14-18m)</span>
          </li>
          <li>
            <span>rozwadniacz boczny</span>
          </li>
          <li>
            <span>filtry liniowe</span>
          </li>
          <li>
            <span>znacznik pianowy</span>
          </li>
        </ul>
      </div>
      <div className="box">
        <ItemInfo
          className="table-info"
          rawNumber={3}
          colNumber={6}
          raw1={[
            "Symbol",
            "Klara 812",
            "Klara 814",
            "Klara 815",
            "Klara 816",
            "Klara 818",
          ]}
          raw2={["Szeroko lancy [m]", "12", "14", "15", "16", "18"]}
          raw3={["Waga bez cieczy", "386", "465", "477", "488", "567"]}
        />
        <PrizeBtn />
        <div className="assets">
          <LightBox assets={[asset1, asset2]} />
        </div>
      </div>
    </div>

    <Footer />
  </div>
);

export default Klara800;
