import React from "react";
import Navigation from "../../components/Navigation";
import Footer from "../../components/Footer";
import ItemInfo from "../../components/ItemInfo";
import LightBox from "../../components/Lightbox";
import asset1 from "../../images/14opryskiwacze-zawieszane/Opryskiwacz zawieszany 815/[UYTR76WSARYdgCBVVLGIOIR7E6SRYDX (1).jpg";
import asset2 from "../../images/14opryskiwacze-zawieszane/Opryskiwacz zawieszany 815/[UYTR76WSARYdgCBVVLGIOIR7E6SRYDX (5).jpg";
import PrizeBtn from "../../components/PrizeBtn";
import data from "../../data";

const info = data.filter((item) => {
  if (item.route === "/opryskiwacze-zawieszane/promar300") {
    return item;
  }
});
const Promar300 = () => (
  <div>
    <Navigation />
    <div className="wrapper">
      <h1 className="product-title">{info[0].name}</h1>
      <div className="description">
        <ul>
          <h3>Wyposaenie standardowe</h3>
          <li>
            <span>
              Zbiornik 300/400 litrw z Polietylenu z dodatkowym zbiorniczkiem
              na czyst wod do mycia rk
            </span>
          </li>
          <li>
            <span>
              Szeroko belki 10, 12 m z regulacj wysokoci za pomoc rcznej
              wcigarki, rcznie rozkadana
            </span>
          </li>
          <li>
            <span>
              Pompa 100/min zapewnia odpowiedni wydajno, a komora
              powietrznika ma eliminowa pulsacj cieczy(Agroplast 100)
            </span>
          </li>

          <li>
            <span>
              Manometr glicerynowy gwarantuje precyzyjno i przejrzysto
              wskaza cinienia roboczego
            </span>
          </li>
          <li>
            <span>Rozwadniacz rodkw chemicznych w sicie wlewowym</span>
          </li>
          <li>
            <span>
              Pojedycze oprawy rozpylaczy z zaworkiem przeciwkroplowym firmy
              AGROPLAST
            </span>
          </li>
          <li>
            <span>
              Rama oraz belka opryskiwacza, oczyszczona w procesie rutowania
              nastpnie malowana proszkowo co zapewnia najlepsze zabezpieczenie
              antykorozyjne
            </span>
          </li>
          <li>
            <span>300l, 400l na 12m stabilizacja belki mechaniczna</span>
          </li>
        </ul>
        <ul>
          <h3>Wyposaenie dodatkowe</h3>
          <li>
            <span>Potrjne oprawy rozpylacza</span>
          </li>

          <li>
            <span>Rozdzielacz staocinieniowy Tolmet 3-sekcyjny</span>
          </li>
          <li>
            <span>Zmiana pompy AP 100 na wosk 100 l/min</span>
          </li>
          <li>
            <span>Zmiana pompy AP 100 na wosk 140 l/min</span>
          </li>
          <li>
            <span>Podnoszenie hydrauliczne</span>
          </li>
          <li>
            <span>Dysze eektorowe ceramiczne ASJ</span>
          </li>
          <li>
            <span>Dysze eektorowe dwustrumieniowe ASJ</span>
          </li>
          <li>
            <span>Znacznik pianowy</span>
          </li>
          <li>
            <span>Owietlenie drogowe</span>
          </li>
          <li>
            <span>Rozwadniacz woski Polmac 35l</span>
          </li>
        </ul>
      </div>
      <div className="box">
        <ItemInfo
          className="table-info"
          rawNumber={7}
          colNumber={2}
          raw1={["Pojemno zbiornika [l]", "300/400"]}
          raw2={["Typ zbiornika", "polietylenowy"]}
          raw3={["Zbiornik na wod do mycia rk", "tak"]}
          raw4={["Szeroko belki [m]", "10/12"]}
          raw5={["Rozpylacze", "szczelinowe Agroplast"]}
          raw6={["Pompa", "Agroplast P-100"]}
          raw7={["Waga [kg]", "150"]}
        />
        <PrizeBtn />
        <div className="assets">
          <LightBox assets={[asset1, asset2]} />
        </div>
      </div>
    </div>

    <Footer />
  </div>
);

export default Promar300;
