import React from "react";
import Navigation from "../../components/Navigation";
import Footer from "../../components/Footer";
import ItemInfo from "../../components/ItemInfo";
import LightBox from "../../components/Lightbox";
import asset1 from "../../images/14opryskiwacze-zawieszane/Opryskiwacz zawieszany 815/[UYTR76WSARYdgCBVVLGIOIR7E6SRYDX (1).jpg";
import asset2 from "../../images/14opryskiwacze-zawieszane/Opryskiwacz zawieszany 815/[UYTR76WSARYdgCBVVLGIOIR7E6SRYDX (5).jpg";
import PrizeBtn from "../../components/PrizeBtn";
import data from "../../data";

const info = data.filter((item) => {
  if (item.route === "/opryskiwacze-zawieszane/promar600") {
    return item;
  }
});
const Promar600 = () => (
  <div>
    <Navigation />
    <div className="wrapper">
      <h1 className="product-title">{info[0].name}</h1>
      <div className="description">
        <ul>
          <h3>Wyposaenie standardowe</h3>
          <li>
            <span>
              Zbiornik 600/800 litrw z Polietylenu z dodatkowym zbiorniczkiem
              na czyst wod do mycia rk
            </span>
          </li>
          <li>
            <span>
              Szeroko belki 12, 15 m z regulacj wysokoci za pomoc rcznej
              wcigarki, rcznie rozkadana
            </span>
          </li>
          <li>
            <span>Stabilizacja mechaniczna belki</span>
          </li>
          <li>
            <span>
              Pompa woska UDOR 100l/min(przy belce 15m), pompa Agroplast
              100l/min(przy belce 12m)
            </span>
          </li>
          <li>
            <span>
              Manometr glicerynowy gwarantuje precyzyjno i przejrzysto
              wskaza cinienia roboczego
            </span>
          </li>
          <li>
            <span>Rozwadniacz rodkw chemicznych w sicie wlewowym</span>
          </li>
          <li>
            <span>
              Pojedycze oprawy rozpylaczy z zaworkiem przeciwkroplowym firmy
              AGROPLAST
            </span>
          </li>
          <li>
            <span>
              Rama oraz belka opryskiwacza, oczyszczona w procesie rutowania
              nastpnie malowana proszkowo co zapewnia najlepsze zabezpieczenie
              antykorozyjne
            </span>
          </li>
          <li>
            <span>rczne rozkadanie</span>
          </li>
          <li>
            <span>
              zbiornik do mycia ukadu(przy zbiorniku 600l i belce 15m oraz przy
              zbiorniku 800l dla obu szerokoci belek)
            </span>
          </li>
        </ul>
        <ul>
          <h3>Wyposaenie dodatkowe</h3>
          <li>
            <span>
              Potrjne oprawy rozpylaczy w formie gowicy z zaworkiem
              przeciwkroplowym oraz dysz do RSM-u
            </span>
          </li>
          <li>
            <span>
              Rozdzielacz staocinieniowy woskiej firmy ARAG (5 sekcji
              roboczych) lub Tolmet (3 lub 4 sekcje)
            </span>
          </li>
          <li>
            <span>
              Pompa woskiej produkcji Udor o wydajnoci 100 l/min lub 140l/min
            </span>
          </li>
          <li>
            <span>Siownik podnoszenia belki</span>
          </li>
          <li>
            <span>Siownik stabilizacji belki 15m</span>
          </li>
          <li>
            <span>Sterowanie elektryczne funkcj praca - przelew</span>
          </li>
          <li>
            <span>
              Sterowanie elektryczne cinieniem i sekcjami opryskiwacza
            </span>
          </li>
          <li>
            <span>Komputer BRAVO 180</span>
          </li>
          <li>
            <span>Rozpylacze specjalistyczne eektorowe</span>
          </li>
          <li>
            <span>Znacznik pianowy</span>
          </li>
          <li>
            <span>Owietlenie drogowe</span>
          </li>
          <li>
            <span>Rozwadniacz woski Polmac 35l</span>
          </li>
          <li>
            <span>Zmiana pompy AP 100 na wosk 100 l/min</span>
          </li>
          <li>
            <span>Zmiana pompy AP 100 na wosk 140 l/min</span>
          </li>
          <li>
            <span>Zmiana pompy woskiej 100 na wosk 140 l/min</span>
          </li>
        </ul>
      </div>
      <div className="box">
        <ItemInfo
          className="table-info"
          rawNumber={7}
          colNumber={2}
          raw1={["Pojemno zbiornika [l]", "600/800"]}
          raw2={["Typ zbiornika", "polietylenowy"]}
          raw3={["Zbiornik na wod do mycia rk", "tak"]}
          raw4={["Szeroko belki [m]", "12/15"]}
          raw5={["Rozpylacze", "szczelinowe Agroplast"]}
          raw6={["Pompa", "Agroplast P-100"]}
          raw7={["Waga [kg]", "250"]}
        />
        <PrizeBtn />
        <div className="assets">
          <LightBox assets={[asset1, asset2]} />
        </div>
      </div>
    </div>

    <Footer />
  </div>
);

export default Promar600;
