import React from "react";
import Navigation from "../../components/Navigation";
import Footer from "../../components/Footer";
import ItemInfo from "../../components/ItemInfo";
import LightBox from "../../components/Lightbox";
import asset1 from "../../images/14opryskiwacze-zawieszane/opryskiwacz zawieszany HU/DSCN2288.JPG";
import asset2 from "../../images/14opryskiwacze-zawieszane/opryskiwacz zawieszany HU/DSCN2291.JPG";
import asset3 from "../../images/14opryskiwacze-zawieszane/opryskiwacz zawieszany HU/DSCN4609.JPG";
import asset4 from "../../images/14opryskiwacze-zawieszane/opryskiwacz zawieszany HU/DSCN4611.JPG";
import asset5 from "../../images/14opryskiwacze-zawieszane/opryskiwacz zawieszany HU/hg798f6 (4).JPG";
import PrizeBtn from "../../components/PrizeBtn";
import data from "../../data";

const info = data.filter((item) => {
  if (item.route === "/opryskiwacze-zawieszane/promar-hu") {
    return item;
  }
});
const PromarHU = () => (
  <div>
    <Navigation />
    <div className="wrapper">
      <h1 className="product-title">{info[0].name}</h1>
      <div className="description">
        <ul>
          <h3>Wyposaenie standardowe</h3>
          <li>
            <span>
              Zbiornik woskiej produkcji o pojemnoci 800l lub 1000l lub
              1200l(dla belki 21m 1000l lub 1200l) wykonany z polietylenu. W
              jednej bryle ukryte s trzy zbiorniki: gwny, na czyst wod oraz
              do mycia rk
            </span>
          </li>
          <li>
            <span>
              Belka polowa o szerokoci 15 lub 18 lub 21m. Podnoszona i
              rozkadana hydraulicznie. Sterowanie belk odbywa si dziki
              dwigniom przeniesionym do kabiny cignika a poczonych z
              rozdzielaczem zamontowanym na opryskiwaczu. Tylko jedna para
              przewodw zasilajcych rozdzielacz hydrauliczny
            </span>
          </li>
          <li>
            <span>Stabilizacja hydrauliczna belki</span>
          </li>
          <li>
            <span>
              Pompa woskiej produkcji UDOR 140/min(dla belki 21m 200l/min)
              zapewnia odpowiedni wydajno, a komora powietrznika ma
              eliminowa pulsacj cieczy
            </span>
          </li>
          <li>
            <span>
              Mieszado hydrauliczne (2szt) zapewnia utrzymanie jednakowego
              stenia cieczy roboczej
            </span>
          </li>
          <li>
            <span>
              Manometr glicerynowy gwarantuje precyzyjno i przejrzysto
              wskaza cinienia roboczego
            </span>
          </li>

          <li>
            <span>Czterostopniowy system filtrowania cieczy</span>
          </li>
          <li>
            <span>Rozwadniacz rodkw chemicznych w sicie wlewowym</span>
          </li>
          <li>
            <span>
              Rozdzielacz staocinieniowy (5 sekcji roboczych) zapewniajcy
              stae cinienie bez wzgldu na zmian iloci pracujcych sekcji
            </span>
          </li>
          <li>
            <span>Ukad cieczowy w rurkach kwasoodpornych</span>
          </li>
          <li>
            <span>
              Pojedyncze oprawy rozpylaczy z zaworkiem przeciwkroplowym
            </span>
          </li>
          <li>
            <span>
              Rama oraz belka opryskiwacza, oczyszczona w procesie rutowania
              nastpnie malowana proszkowo co zapewnia najlepsze zabezpieczenie
              antykorozyjne
            </span>
          </li>
          <li>
            <span>Owietlenie drogowe</span>
          </li>
        </ul>
        <ul>
          <h3>Wyposaenie dodatkowe</h3>
          <li>
            <span>
              Potrjne oprawy rozpylaczy w formie gowicy z zaworkiem
              przeciwkroplowym oraz dysz do RSM-u
            </span>
          </li>
          <li>
            <span>
              Elektryczne sterowanie rozkadaniem belki (elektrozawory)
            </span>
          </li>
          <li>
            <span>Sterowanie elektryczne funkcj praca - przelew</span>
          </li>
          <li>
            <span>
              Sterowanie elektryczne cinieniem i sekcjami opryskiwacza
            </span>
          </li>
          <li>
            <span>Belka polowa dzielona na 7 sekcji</span>
          </li>
          <li>
            <span>Komputer BRAVO 180</span>
          </li>
          <li>
            <span>Komputer BRAVO 300 S</span>
          </li>
          <li>
            <span>Komputer BRAVO 400 z nawigacj</span>
          </li>
          <li>
            <span>
              Antena GPS do komputera BRAVO 180Si BRAVO 300S (pomiar prdkoci
              bez montowania czujnika w cigniku)
            </span>
          </li>
          <li>
            <span>Rozpylacze specjalistyczne eektorowe</span>
          </li>
          <li>
            <span>Rozwadniacz boczny woskiej produkcji POLMAC</span>
          </li>
          <li>
            <span>Puczka zbiornika</span>
          </li>
          <li>
            <span>Znacznik pianowy</span>
          </li>
          <li>
            <span>Owietlenie ledowe belki</span>
          </li>
          <li>
            <span>Pompa 170l/min</span>
          </li>
        </ul>
      </div>
      <div className="box">
        <ItemInfo
          className="table-info"
          rawNumber={7}
          colNumber={2}
          raw1={["Pojemno zbiornika [l]", "800/1000/1200"]}
          raw2={["Typ zbiornika", "3w1 polietylenowy"]}
          raw3={["Zbiornik na wod do mycia rk", "tak"]}
          raw4={["Szeroko belki [m]", "15/18/21"]}
          raw5={["Rozpylacze", "szczelinowe Agroplast"]}
          raw6={["Pompa", "UDOR 170 l/min"]}
          raw7={["Waga [kg]", "730"]}
        />
        <PrizeBtn />
        <div className="assets">
          <LightBox assets={[asset1, asset2, asset3, asset4, asset5]} />
        </div>
      </div>
    </div>

    <Footer />
  </div>
);

export default PromarHU;
